<template>
    <div class="bindCard">
        <c-top-back></c-top-back>
        <r-group>
            <r-cell title="" class="cellTitle"></r-cell>
            <r-cell title="" :value="cardBank" is-link @click.native="cardBankClick"></r-cell>
            <r-cell title="" :value="provinceName +' '+cityName" is-link @click.native="cityCodeClick"></r-cell>
        </r-group>
        <r-group>
            <r-cell title="" :value="openingBank" is-link @click.native="openingBankClick" class="wd"></r-cell>
            <r-input title="" :max="21" v-model="cardNumber" placeholder=""></r-input>
        </r-group>
        <r-group v-if="type==1">
            <r-input title="" :max="20" v-model="trueName" placeholder=""></r-input>
            <r-input title="" :max="18" v-model="idCode" placeholder=""></r-input>
        </r-group>
        <r-group v-else>
            <r-input title="" :max="50" v-model="companyName" placeholder=""></r-input>
            <r-input title="" :max="20" v-model="license" placeholder=""></r-input>
        </r-group>
        <div class="btn">
            <r-button type="primary" text="" @click.native="btnClick" :disabled="btnDisabled"></r-button>
        </div>
        <!---->
        <r-address-checked :defaultStatus="cardBankCode"
                           v-model="showBank"
                           :address="bankList"
                           @get-addressid="getBank"
                           @address-add="showBank=false" btn-txt="" title=""></r-address-checked>
        <!---->
        <r-address-checked
                :defaultStatus="openingBankCode"
                v-model="showSubBank"
                :address="subBankList"
                @get-addressid="getSubBank"
                @address-add="showSubBank=false" btn-txt="" title=""></r-address-checked>
        <!---->
        <r-popup v-model="showProvince" :hide-on-blur="false">
            <div class="popupArea">
                <h3> <a href="javascript:;" class="close" @click="showProvince=false"></a></h3>
                <div class="areaContent" :style="{maxHeight:maxH,overflow: 'auto'}">
                    <r-group>
                        <r-radio :options="province" :value="provinceValue" @on-change="changeProvince"></r-radio>
                    </r-group>
                </div>
            </div>
        </r-popup>
        <!---->
        <r-popup v-model="showCity" :hide-on-blur="false">
            <div class="popupArea">
                <h3><a href="javascript:;" class="close" @click="showCity=false"></a></h3>
                <div class="areaContent" :style="{maxHeight:maxH,overflow: 'auto'}">
                    <r-group>
                        <r-radio :options="city" :value="cityValue" @on-change="changeCity"></r-radio>
                    </r-group>
                </div>
            </div>
        </r-popup>

        <!---->
        <r-popup v-model="showTxBind" height="100%">
            <div class="txBind">
                <c-top-back :Call="true" @on-change="showTxBind=false"></c-top-back>
                <r-group>
                    <r-cell title="" :value="mobile"></r-cell>
                    <r-input title="" :max="6" v-model="yzm" class="weui_vcode" placeholder="">
                        <r-button slot="right" type="primary" class="sendYzmBtn" :disabled="sendYzmBtn"
                                  @click.native="sendCodeBtn">{{txt}}
                        </r-button>
                    </r-input>
                </r-group>
                <div class="agreement"><label><input type="radio" value="1" v-model="radioValue"/></label><a
                        href="javascript:;" @click="agreementClick"></a></div>
                <div class="btn">
                    <r-button type="primary" text="" @click.native="bindBtnClick"
                              :disabled="btnDisabledBind"></r-button>
                </div>
                <r-popup v-model="agreement">
                    <div class="agreementCon" :style="{height:h}">
                        <div class="desc">
                            
                            <p>
                                1.</p>
                            <p>2.</p>
                            <p>
                                3.</p>
                            <p>4.,</p>
                            <p>5.</p>
                            <p>
                                6./</p>
                            <p>7. </p>
                            <p>8.</p>
                            <p>
                                9.()</p>
                            <p>
                                10.</p>
                            <p>
                                11.</p>
                            <p>
                                12., </p>
                            <p>
                                13.</p>
                            <p>14. </p>
                            <p>15.</p>
                        </div>
                        <div class="submit">
                            <r-button type="primary" @click.native="agreement=false;radioValue=1"></r-button>
                        </div>
                    </div>
                </r-popup>
            </div>
        </r-popup>
    </div>
</template>

<script>
    import utils from '../../libs/utils.js';
    export default{
        data(){
            return {
                type: this.$route.query.type,
                provinceName: "",//
                provinceCode: "",//
                cityName: "",//
                cityCode: "",//
                cardBank: "",//
                cardBankCode: "",//
                cardAddress: "",
                openingBank: "",//
                openingBankCode: "",//
                cardNumber: "",
                trueName: "",
                idCode: "",
                bankList: [],
                btnDisabled: true,
                showBank: false,
                showSubBank: false,
                subBankList: [],

                //
                license: "",
                companyName: "",

                //
                showProvince: false,
                province: [],//
                provinceValue: {},//
                showCity: false,
                city: [],//
                cityValue: {},//
                proStatus: 0,
                proNum: 0,


                //
                mobile: localStorage.mobile,
                showTxBind: false,
                yzm: "",
                sendYzmBtn: false,
                txt: "",
                btnDisabledBind: true,
                radioValue: "",
                agreement: false,
                sn: "",
                showId: "",

            }
        },
        computed: {
            maxH(){
                return $(window).height() * 0.5 + "px";
            },
            h(){
                return $(window).height() + "px";
            }
        },
        mounted: function () {
            this.$nextTick(function () {
                let that = this;
                //
                that.trueName = that.$route.query.name;
                that.idCode = that.$route.query.num;

                that.companyName = that.$route.query.company;
                that.license = that.$route.query.buslince;

                //
                utils.ajax({
                    url: "/seller/account/getBankInfo",
                    success: function (res) {
                        if (res.code == "SUCCESS") {
                            that.bankList = [];
                            res.result.forEach(function (obj, i) {
                                that.bankList.push({id: obj.bankcode, trueName: obj.bankname});
                            });
                        } else {
                            that.$vux.alert.show(res.message);
                        }
                    }
                });

                //
                utils.ajax({
                    url: "/seller/account/getBankCity",
                    success: function (res) {
                        if (res.code == "SUCCESS") {
                            that.province = [];
                            res.result.forEach(function (obj, i) {
                                that.province.push({key: obj.code, value: obj.name});
                            })
                        } else {
                            that.$vux.alert.show(res.message);
                        }
                    }
                });

                //
                if (this.$route.query.rzStatus == "rzsb") {
                    utils.ajax({
                        url: "/seller/account/getStoreBankCard",
                        success: function (res) {
                            if (res.code == "SUCCESS") {
                                let json = res.result;
                                that.showId = json.id;
                                that.cardBank = json.bank_name;
                                that.cardBankCode = json.bank_code;
                                //json.bank_province_name = "";
                                that.provinceName = json.bank_province_name;
                                that.provinceCode = json.bank_province_code;
                                that.provinceValue = {key: that.provinceCode, value: that.provinceName};

                                //json.bank_city_name = "";
                                that.cityName = json.bank_city_name;
                                that.cityCode = json.bank_city_code;
                                that.cityValue = {key: that.cityCode, value: that.cityName};

                                that.openingBank = json.bank_branch_name;
                                that.openingBankCode = json.bank_branch_code;

                                that.cardNumber = "";//
                                that.companyName = json.company_name;
                                that.license = json.buslince;

                                //
                                utils.ajax({
                                    url: "/seller/account/getBankInfo",
                                    data: {citycode: that.cityCode, bankcode: that.cardBankCode},
                                    success: function (res) {
                                        if (res.code == "SUCCESS") {
                                            that.subBankList = [];
                                            res.result.forEach(function (obj, i) {
                                                that.subBankList.push({id: obj.bankcode, trueName: obj.bankname});
                                            });
                                        } else {
                                            that.$vux.alert.show(res.message);
                                        }
                                    }
                                })
                            } else {
                                that.$vux.alert.show(res.message);
                            }
                        }
                    });
                }
            })
        },
        methods: {
            //
            cardBankClick(){
                this.showBank = true;
            },
            //
            cityCodeClick(){
                if (this.cardBank) {
                    this.showProvince = true;
                } else {
                    this.$vux.alert.show("");
                }
            },
            //
            openingBankClick(){
                if (this.cityName) {
                    this.showSubBank = true;
                } else {
                    this.$vux.alert.show("");
                }

            },
            //
            getBank(id, obj){
                let that = this;
                this.cardBankCode = id;
                this.cardBank = obj.trueName;
                this.showBank = false;
//                this.proNum++;
//                if (this.proNum > 1) {
//                    this.provinceValue = {};
//                    this.cityValue = {};
//                }

                //

                this.provinceName = "";
                this.provinceCode = "";


                this.cityName = "";
                this.cityCode = "";
                this.openingBank = "";//
                this.openingBankCode = "";//
            },
            //
            getSubBank(id, obj){
                this.openingBankCode = id;
                this.openingBank = obj.trueName;
                this.showSubBank = false;
            },
            //
            check(){
                if (this.cardBank != "" && this.cityCode != "" && this.openingBank != "" && /^\d{16,21}$/.test(this.cardNumber) && this.trueName.indexOf(" ") == -1 && this.trueName != "" && /^(\d{6})(\d{4})(\d{2})(\d{2})(\d{3})([0-9]|X)$/.test(this.idCode)) {
                    this.btnDisabled = false;
                } else {
                    this.btnDisabled = true;
                }
            },
            //
            checkCompany(){
                if (this.cardBank != "" && this.cityCode != "" && this.openingBank != "" && /^\d{16,21}$/.test(this.cardNumber) && this.license.indexOf(" ") == -1 && this.license != "" && this.companyName.indexOf(" ") == -1 && this.companyName != "") {
                    this.btnDisabled = false;
                } else {
                    this.btnDisabled = true;
                }
            },
            //
            btnClick(){
                let that = this;
                that.showTxBind = true;
            },
            //
            changeProvince (obj) {
                this.proStatus++;
                let that = this;
                that.provinceName = obj.value;
                that.provinceCode = obj.key;
                if (this.$route.query.rzStatus == "" || this.$route.query.rzStatus == undefined) {
                    this.proStatus = 2;
                }
                if (this.proStatus > 1) {
                    //
                    utils.ajax({
                        url: "/seller/account/getBankCity",
                        data: {citycode: obj.key},
                        success: function (res) {
                            if (res.code == "SUCCESS") {
                                that.city = [];
                                res.result.forEach(function (obj, i) {
                                    that.city.push({key: obj.code, value: obj.name});
                                });
                                that.showProvince = false;
                                that.showCity = true;
                            } else {
                                that.$vux.alert.show(res.message);
                            }
                        }
                    })
                }
            },
            //
            changeCity(obj){
                let that = this;
                this.cityName = obj.value;
                this.showCity = false;
                this.cityCode = obj.key;
                //
                utils.ajax({
                    url: "/seller/account/getBankInfo",
                    data: {citycode: obj.key, bankcode: that.cardBankCode},
                    success: function (res) {
                        if (res.code == "SUCCESS") {
                            that.subBankList = [];
                            res.result.forEach(function (obj, i) {
                                that.subBankList.push({id: obj.bankcode, trueName: obj.bankname});
                            });
                        } else {
                            that.$vux.alert.show(res.message);
                        }
                    }
                })
            },

            //
            checkTxBind(){
                if (this.yzm.length == 6 && this.radioValue == 1) {
                    this.btnDisabledBind = false;
                } else {
                    this.btnDisabledBind = true;
                }
            },
            sendCodeBtn(){
                let that = this;
                var second = 59;
                that.sendYzmBtn = true;
                utils.ajax({
                    url: "/seller/seller/verify",
                    data: {phone: that.mobile},
                    success: function (res) {
                        if (res.code == "SUCCESS") {
                            that.sn = res.result.sn;
                            that.txt = "(" + second + ")";
                            var timer = setInterval(function () {
                                if (second > 1) {
                                    second--;
                                    that.txt = "(" + second + ")";
                                } else {
                                    clearInterval(timer);
                                    that.txt = '';
                                    that.sendYzmBtn = false;
                                }
                            }, 1000);
                        } else {
                            that.sendYzmBtn = false;
                            that.$vux.alert.show(res.message);
                        }
                    }
                });

            },
            bindBtnClick(){
                let that = this;
                let param = {
                    "user_name": that.trueName,
                    "certificate_number": that.idCode,
                    "card_no": that.cardNumber,
                    "bank_code": that.cardBankCode,
                    "bank_name": that.cardBank,
                    "bank_branch_code": that.openingBankCode,
                    "bank_branch_name": that.openingBank,
                    "bank_province_code": that.provinceCode,
                    "bank_city_code": that.cityCode,
                    "company_name": that.companyName,
                    "buslince": that.license,
                    "phone": that.mobile,
                    "sn": that.sn,
                    "verfiy_code": that.yzm,
                    bank_province_name: that.provinceName,
                    bank_city_name: that.cityName,
                    certificate_type: 0,
                    buslince_pic: "",
                };
                if (that.$route.query.rzStatus == "rzsb") {
                    param.id = that.showId;
                    utils.ajax({
                        url: "/seller/account/updateCompanyStoreBankCard",
                        data: param,
                        success: function (res) {
                            if (res.code == "SUCCESS") {
                                that.$vux.toast.show('');
                                setTimeout(function () {
                                    utils.go("/tx/balance", that.$router);
                                }, 2000)
                            } else {
                                that.$vux.alert.show({
                                    content: res.message,
                                    onHide(){
                                        that.showTxBind = false;
                                    }
                                });
                            }
                        }
                    })
                } else {
                    utils.ajax({
                        url: "/seller/account/addStoreBankCard",
                        data: param,
                        success: function (res) {
                            if (res.code == "SUCCESS") {
                                that.$vux.toast.show('');
                                setTimeout(function () {
                                    utils.go("/tx/balance", that.$router);
                                }, 2000)
                            } else {
                                that.$vux.alert.show(res.message);
                            }
                        }
                    })
                }

            },
            //
            agreementClick(){
                this.agreement = true;
            }
        },
        watch: {
            cardBank(val, oldVal){
                if (this.type == 1) {
                    this.check();
                } else {
                    this.checkCompany();
                }
            },
            cityCode(val, oldVal){
                if (this.type == 1) {
                    this.check();
                } else {
                    this.checkCompany();
                }
            },
            openingBank(val, oldVal){
                if (this.type == 1) {
                    this.check();
                } else {
                    this.checkCompany();
                }
            },
            cardNumber(val, oldVal){
                if (this.type == 1) {
                    this.check();
                } else {
                    this.checkCompany();
                }
            },
            trueName(val, oldVal){
                this.check();
            },
            idCode(val, oldVal){
                this.check();
            },
            companyName(val, oldVal){
                this.checkCompany();
            },
            license(val, oldVal){
                this.checkCompany();
            },
            //
            yzm(val, oldVal){
                this.checkTxBind();
            },
            radioValue(val, oldVal){
                this.checkTxBind();
            },

        },
        components: {
            "cTopBack": require('../../components/x-top-back/x-top-back.vue'),
            "rCell": require("../../components/cell/cell.vue"),
            "rGroup": require("../../components/group/group.vue"),
            "rInput": require("../../components/input/input.vue"),
            "rButton": require("../../components/button/button.vue"),
            "rAddressChecked": require('../../components/x-address-checked/x-address-checked.vue'),
            "rAddress": require("../../components/address/address.vue"),
            "rPopup": require("../../components/popup/popup.vue"),
            "rRadio": require("../../components/radio/x-radio.vue"),
        }
    }
    //require("../../assets/styles/views/batchExport.less");
</script>
<style lang="less">
    @import "../../assets/styles/views/personal";
</style>



